
.header{
    background-color:white;
    color: white;
    display: flex;
    justify-content: space-between;
    height: 10vh;
    align-items: center;
    font-weight: 400;
    font-size: 17px;
    position: sticky;
    top: 0;
    z-index: 99;
    
}
header a:hover{
    text-decoration: none;
    color: #4ba49f;
}



.logo{
    font-family: rr-beaver;
    font-size: 2.5em;
    text-decoration: none;
}

.logo:hover{
    text-decoration: none;
    color: #4ba49f;
}
.menu{
    /* .menu hereda de .header */
    height: inherit;
}
figure{
    margin: 0;
}

.header ol{
    display: flex;
    /* .header ol hereda de .menu */
    height: inherit;
}

.header ol  { /* .header ol li hereda de .header ol */
    height: inherit;
    align-items: center;
  }


  .header li{
    margin-right: 20px;
  }



  header a {
    color: black;
    text-decoration: none;
    /* .header a hereda de .header ol li */
    height: inherit;
    display: flex;
  align-items: center;


  padding: 0 10px; 
}

.bandera{
    border-radius: 5px;
    vertical-align: middle;
}
.contenedor{
    width:85%;
    margin: 0 auto;
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    height: inherit;
    flex-wrap: inherit;

}
.contenedor_reducido{
    width: 65%;
    margin: 0 auto;
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    height: inherit;
    flex-wrap: inherit;
}
.btn_contacto{
    padding: 10px;

    border: none;
    border-radius: 10px;
    color: white;
    font-size: medium;
}
.contacto{
    margin-bottom: 15vh;
    margin-top: 15vh;
}

.btn_contacto:hover{
    
    cursor:pointer;
    color:#F5F5F5
    
}

header ol,ul{
    margin: 0;
    padding: 0;
    list-style:  none;
}




