
.nav{
    background-color:#e3b704;
    color: rgb(10, 10, 10);
    display: flex;
    justify-content: space-between;
    height: 5vh;
    align-items: center;
    border:2px;
    
    font-weight: 400;
    font-size: 17px;
    position: sticky;
    top: 0;
    z-index: 99;
    border-radius: 10px;
    
}
nav a:hover{
    text-decoration: none;
    color: #4ba49f;
}



.logo{
    font-family: rr-beaver;
    font-size: 2.5em;
    text-decoration: none;
}

.logo:hover{
    text-decoration: none;
    color: #4ba49f;
}
.menu{
    /* .menu hereda de .header */
    height: inherit;
}
figure{
    margin: 0;
}

.nav ol{
    display: flex;
    /* .header ol hereda de .menu */
    height: inherit;
}

.nav ol  { /* .header ol li hereda de .header ol */
    height: inherit;
    align-items: center;
  }


  .nav li{
    margin-right: 10px;
  }



  nav a {
    color: #030303;
    text-decoration: none;
    /* .header a hereda de .header ol li */
    height: inherit;
    display: flex;
  align-items: center;

  padding: 0 10px; 
}

.bandera{
    border-radius: 5px;
    vertical-align: middle;
}


.contenedor{
    width:85%;
    margin: 0 auto;
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    height: inherit;
    flex-wrap: inherit;
 

}
.contenedor_reducido{
    width: 65%;
    margin: 0 auto;
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    height: inherit;
    flex-wrap: inherit;
}


nav ol,ul{
    margin: 0;
    padding: 0;
    list-style:  none;
}

.nav h6{
    height:1vh;
    padding: 0 20px 0 0;
    color: white;
}

.nav-img{
    padding: 0 70px 0 0;
}