
@media screen and (min-width:1300px){
    .hero_text2{
        width: 70%;
        font-size: 1.2em;
    }
}

@media screen and (max-width:1024px){
    .contenedor{
        width: 90%;
    }  

    .tecnologia_titulo{
        text-align: start;
        margin-left: 0;
        padding-left: 3%;
    }
    .contenedor_tecnologia{
        display: block;
    }
    .tecnologia_items{
        width: 100%;
    }
    
    
}

@media  screen and (max-width: 758px){
    .header{
        display: block;
    }

    .etiqueta_text_informacion {
    left: 39%;
    }
    

    .burger-button{
        display: initial;
    }
    .menu{
        position: fixed;
        background:rgba(2, 95, 255,0.8);
        height: auto;
        width: 100vw;
        top: 10vh;
        justify-content: center;
        align-items: center;
        left: -100vw;
        height: 90vh;
        
        font-size: larger;
        transition: .3s;
        
        
    }

    .menu.is-active{
        left: 0;
    }

    .burger-button.is-active{
        background-color: rgba(77, 75, 75, 0.8);
    }
    .header ol{
        display: flex;
        flex-direction: column;
        justify-content: center ;
        
    }
    .header ol li {
        height: 50px;
    }
    .header a {
        justify-content: center;
    }
    .header li{
        text-align: center;
    }
    
    
    .proyecto{
        height: auto;
        
    }
    
    
    
    .item p{
        display: none;
    }

