.covid-container{
    display: flex;
    background-color: white;
    color: black;
    height: 70vh;
    padding: 100px 0 100px 0;
}

.covid-desc > h2{
    align-items: center;
    padding: 0 0 50px 650px;
}

.row{
    align-items: center;
    padding: 0 0 0px 100px;
}

.covid-img{
    width: 80%;
    display: flex;
    align-self: center;
}

.covid-desc > p{
    align-items: center;
    padding: 0 200px 50px 120px;
}