.image-container{
    height: 100vh;
    width: 90%;
    object-fit: contain;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: block;
    
}

.image{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    border-radius: 80px;
    padding: 0 30px 30px 30px ;
    
}

.taxi-container{
    height: 200vh;
    width: 110%;
    object-fit: contain;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}





