.carta {
    width: 100%;
    height: 200px;
    background: #333;
    color: #fff;
    font-size: 30px;
    margin: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Carousel {
    vertical-align: center;
}
.img{
    glyph-orientation-vertical: auto;
    
    
}
.Gallery-desc > h2{
    padding: 0 0 0 110px;
}
.Gallery-desc > p{
    padding: 0 0 0 110px;
}

.tecnologia_items::-webkit-scrollbar {
    -webkit-appearance: none;

}

.tecnologia_items::-webkit-scrollbar:horizontal {
    height: 10px;
}

.tecnologia_items::-webkit-scrollbar-thumb {
    background-color: #797979;
    border-radius: 20px;
    border: 2px solid #f1f2f3;
}

.tecnologia_items::-webkit-scrollbar-track {
    border-radius: 10px;  
}

