


.footer-container{
    display: flex;
    background-color: #e3b704;
    height: 90vh;
    z-index: -99;
    color: black;
    

}

.footer-desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 90%;
    height: 60vh;
    padding: 0 100px 100px 100px
}

.footer-desc > h3{
    font-size: 20px;
}

.footer-contacto > h3{
    font-size: 20px;
}

.footer-desc > p{
    font-size: 15px;
}

.footer-imagen-diamon{
    padding: 0 0 100px 0;
}

.footer-contacto-imagen{
    padding: 200px 0 0 0;
}

.footer-imagen > img{
    width:80px;
}
